.section--cookies,
#acceptcookie {
	position: fixed;
	width: calc(100% - 30px);
	bottom: 15px;
	left: 15px;
	z-index: 999;
	text-align: center;

	background: rgba(0, 0, 0, 0.7);
	border-radius: 3px;
	padding: 15px;
	font-size: 14px;
	color: white;

	&.cookies-accepted {
		display: none;
	}

	.btn {
		margin: 0 8px;
	}

}
