$grid-columns: 16 !default;
$grid-gutter-width: 30px !default;
$grid-breakpoints: (xs: 0, sm: 768px, md: 1024px, lg: 1440px, xl: 1830px) !default;
$container-max-widths: (sm: calc(100vw - 120px),
	md: calc(100vw - 180px),
	lg: calc(100vw - 240px),
	xl: 1530px) !default; // calc(100vw - 300px)) !default;
$container-fluid-max-widths: (sm: calc(100vw - 80px),
	md: calc(100vw - 120px),
	lg: calc(100vw - 160px),
	xl: calc(100vw - 200px)) !default; // calc(100vw - 300px)) !default;
