.section--form {
	padding-bottom: 100px;
	border-bottom: 2px solid $border;

	.group--cta {
		margin: 60px 0 0 0;
	}
}

.section--picture-text,
.section--text-picture {
	padding-top: 120px;
	padding-bottom: 100px;

	.row {
		align-items: center;
	}

	.category {
		@extend %category;
	}

	.title {
		font-size: 32px;
		font-weight: $font-light;
		line-height: 50px;
		margin-bottom: 40px;
		color: $foreground;

		b {
			font-weight: $font-bold;
		}
	}

	.abstract {
		font-size: 18px;
		line-height: 32px;
		font-weight: $font-light;
		color: $foreground-secondary;

		p {
			margin-bottom: 10px;
		}
	}

	.title {
		font-size: 55px;
		line-height: 1.1;
		font-weight: $font-semibold;
		color: $foreground;
		margin-bottom: 35px;
	}

	.abstract {
		font-size: 38px;
		line-height: 1.1;
		font-weight: $font-regular;
		color: $foreground;
		margin-bottom: 40px;
	}

	.group--cta {
		margin: 40px 0 0 0;
		text-align: left;
	}

	.group--content {
		margin-top: 40px;
	}

	.group--content+.group--content {
		margin-top: 100px;
	}

	&.text--primary {
		padding-top: 140px;
		padding-bottom: 140px;

		.title {
			font-size: 55px;
			line-height: 1.1;
			font-weight: $font-semibold;
			color: $foreground;
			margin-bottom: 35px;
		}

		.abstract {
			font-size: 38px;
			line-height: 1.1;
			font-weight: $font-regular;
			color: $foreground;
			margin-bottom: 40px;
		}

	}

	@include media-breakpoint-down(sm) {
		padding-top: 80px;
		padding-bottom: 60px;

		.picture {
			margin-bottom: 30px;
		}

		.group--cta {
			margin: 30px 0 30px 0;
			align-items: flex-start;
		}
	}
}

.border--top {
	border-top: 2px solid $border;
}

.border--bottom {
	border-bottom: 2px solid $border;
}
